/* main color #ff003b 255,0,59 */
header{
    background-image: url("https://i.redd.it/uwwte8wps4h91.gif");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.header-content {
    width: 100%;
    height: 855px;
    background: rgb(255,0,59,0.5);
    color: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.header-content img {
    width: 272px;
    height: 272px;
    border-radius: 50%;
    box-shadow: 0 0 50px #FFFFFF;
}
.header-content h1 {
    font-size: 42px;
}
.header-content p {
    font-size: 25px;
}